<template>
    <div class="like">
        <van-nav-bar
        title="我的收藏"
        left-text="返回"
        left-arrow
        @click-left="black"
        />
        <BgBox></BgBox>
        <div class="shop-item-box">
            <div class="shop-item" v-for="(item,index) in likeList" :key="index">
                <div class="padding">
                    <div class="shop-item-img">
                        <img :src="item.smallImg" alt="">
                    </div>
                    <div class="shop-desc">
                        <div class="shop-item-name">{{item.name}}</div>
                        <div class="shop-item-oldPrice">￥{{28.00}} 
                            <span class="manjian" @click="delLikeShop(item,index)"><img src="../assets/images/del.png" alt=""></span> 
                        </div>
                        <div class="shop-item-price">￥{{item.price}}</div>
                    </div>
                </div>
            </div>
            
        </div>
    </div>
</template>

<script>
    import BgBox from "../components/BgBox.vue";
    export default {
        name: 'Like',
        components: {
            BgBox
        },
        data(){
            return {
                likeList: []
            }
        },
        created(){
            this.getLikeShop();
        },
        methods: {
            black(){
                this.$router.go(-1);
            },
            getLikeShop(){
                let tokenString = localStorage.getItem("token");

                if (!tokenString) {
                    this.$toast("请先登录");
                    return this.$router.push({ name: "Login" });
                }

                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0,
                }); 
                this.axios({
                    method: 'GET',
                    url: '/findAllLike',
                    params: {
                        appkey: this.appkey,
                        tokenString,
                    }
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == 2000){
                        res.data.result.map(v => {
                            this.likeList.push(v);
                        })
                    }
                }).catch(err => {
                    this.$toast.clear();
                })
            },
            delLikeShop(item,index){
                let pid = item.pid;
                let tokenString = localStorage.getItem("token");

                if (!tokenString) {
                    this.$toast("请先登录");
                    return this.$router.push({ name: "Login" });
                }

                this.$toast.loading({
                    message: "加载中...",
                    forbidClick: true,
                    duration: 0,
                }); 
                this.axios({
                    method: 'POST',
                    url: '/notlike',
                    data: {
                        appkey: this.appkey,
                        tokenString,
                        pid,
                    }
                }).then(res => {
                    this.$toast.clear();
                    if(res.data.code == 900){
                        this.likeList.splice(index,1);
                    }
                    
                }).catch(err => {
                    this.$toast.clear();
                }) 
            }
        },
    }
</script>

<style lang="less" scoped>
    .like{
        background-color: #efefef;
        /deep/.bg-box .bg-content{
            display: none;
        }
    }
    .shop-item-box{
            display: flex;
            flex-wrap: wrap;
            justify-content: space-between;
            margin-top: -20px;
            padding: 0 10px 50px;
            .shop-item{
                width: calc(50% - 10px);
                margin-top: 10px;
                background-color: white;
                border-radius: 10px;
                .padding{
                    padding: 8px 0;
                }
                .shop-item-img{
                    width: 90%;
                    margin: 0 auto;
                    img{
                        border-radius: 10px;
                        width: 100%;
                    }
                }
                .shop-desc{
                    margin-left: 10px;
                    .shop-item-name{
                        font-size: 14px;
                    }
                    .shop-item-oldPrice{
                        text-decoration: line-through;
                        .manjian{
                            text-align: right;
                            border-radius: 5px;
                            padding: 2px;
                            float: right;
                            margin-right: 10px;
                            color: white;
                            img{
                                width: 16px;
                            }
                        }
                        &::after{
                            content: '';
                            display: block;
                            clear: both;
                        }
                    }
                    .shop-item-price{
                        color: #c03015;
                        font-size: 12px;
                        margin-top: 5px;
                    }
                }
            } 
        }
</style>